// FOOTER :: START

.footer {
	position: relative;
	padding-top: 50px;
	padding-bottom: 50px;
	
	&__bg {
		z-index: 0;
		position: absolute;
		bottom: 50px;
		left: 0;
		right: 0;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		max-width: calc(100% - 100px);
		height: 600px;
		border-radius: 10px;
		background-color: #0A0A0A;
	}

	&__wrapper {
		z-index: 2;
		position: relative;

		&-1 {
			overflow: hidden;
			padding: 50px 60px;
			border-radius: 10px;
			background-color: #4097D9;
		}

		&-2 {
			display: flex;
			padding-top: 50px;
			margin-bottom: 50px;
			padding-bottom: 50px;
			border-bottom: 1px solid rgba(#fff, 0.1);

			> div {
				&:nth-of-type(1) {
					display: flex;
					width: 100%;
					max-width: 680px;

					> div {
						&:nth-of-type(1) {
							width: 100%;
							max-width: 230px;
						}
						&:nth-of-type(2) {
							position: relative;
							flex: 1;
							min-width: 1px;
							max-width: 100%;
							padding-left: 20px;
							margin-left: 20px;

							&:after {
								content: '';
								position: absolute;
								top: 50%;
								transform: translateY(-50%);
								left: 0;
								width: 1px;
								height: 30px;
								background: rgba(#FFFFFF, 0.2);
							}

							.footer__heading {
								opacity: 0;
							}
						}
					}
				}
				&:nth-of-type(2) {
					display: flex;
					justify-content: space-between;
					flex: 1;
					min-width: 1px;
					max-width: 100%;
					padding-left: 90px;

					> div {
						white-space: nowrap;
					}
				}
			}
		}

		&-3 {
			display: flex;
			align-items: center;

			> div {
				&:nth-of-type(2) {
					flex: 1;
					min-width: 1px;
					max-width: 100%;
					padding-left: 30px;
					padding-right: 55px;
				}
			}

			.c-btn {
				&:not(:last-of-type) {
					margin-right: 11px;
				}
			}
		}

		&-4 {
			padding-top: 100px;
			padding-bottom: 35px;
		}

		&-bg {
			z-index: 0;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-image: url('../img/img-footer-pattern.svg');
			background-repeat: no-repeat;
			background-position: center top;
			background-size: cover;
		}
	}

	&__form {
		z-index: 2;
		position: relative;

		&-title {
			padding-bottom: 60px;
			font-weight: 600;
			font-size: 20px;
			line-height: 30px;
			color: #FFFFFF;
		}

		&-group {
			display: flex;
			justify-content: space-between;

			> div {
				width: 100%;
				max-width: calc((100% - 80px) / 3);
				padding-bottom: 50px;
			}
		}

		&-field {
			display: flex;
			width: 100%;

			&.footer__form-field--btn {
				justify-content: flex-end;
				padding-top: 15px;
			}

			.c-btn {
				min-width: 140px;
			}

			label {
				display: flex;
				flex-direction: column;
				width: 100%;

				> span {
					display: block;
					@extend %fontOpenSans;
					font-weight: 800;
					font-size: 10px;
					line-height: 14px;
					text-transform: uppercase;
					color: #FFFFFF;
				}
				
				input,
				textarea {
					outline: none;
					position: relative;
					display: flex;
					width: 100%;
					height: 35px;
					padding: 0;
					border: none;
					border-bottom: 2px solid transparent;
					box-shadow: none;
					border-radius: 0;
					background-color: transparent;
					@extend %fontOpenSans;
					font-size: 14px;
					line-height: 19px;
					color: #ffffff;
					@include transition(border-color);
					@include inputPlaceholder {
						@extend %fontOpenSans;
						font-size: 14px;
						line-height: 19px;
						color: rgba(#FFFFFF, 0.7);
					}

					&:focus {
						border-color: #fff;
						@include transition(border-color);
					}
				}

				textarea {
					resize: none;
				}
			}
		}
	}

	&__title {
		padding-bottom: 10px;
		font-weight: 600;
		font-size: 20px;
		line-height: 30px;
		color: #FFFFFF;
	}

	&__desc {
		@extend %fontOpenSans;
		font-size: 14px;
		line-height: 24px;
		color: #FFFFFF;
	}

	&__heading {
		padding-bottom: 10px;
		font-weight: 600;
		font-size: 16px;
		line-height: 24px;
		color: #FFFFFF;
	}

	&__link {
		font-size: 14px;
		line-height: 19px;
		color: rgba(#FFFFFF, 0.8);
	}

	&__address {
		font-size: 14px;
		line-height: 24px;
		color: rgba(#FFFFFF, 0.8);

		img {
			display: inline-block;
			vertical-align: middle;
		}
	}
}

// FOOTER :: END
