// BUTTON :: START

.c-btn {
  outline: none;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  height: 50px;
  padding-left: 32px;
  padding-right: 32px;
  box-shadow: none;
  border-radius: 2rem;

  &__wrapper {
    position: relative;
    display: flex;
    align-items: center;

    &--center {
      justify-content: center;
    }
    &--end {
      justify-content: flex-end;
    }
  }

  &__border {
    border: 1px solid #fff;
    background-color: transparent;
  }

  &__bg {
    overflow: hidden;
    background-color: #00B94F;

    &:after {
      content: '';
      z-index: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(177.56deg, #4097D9 -48.98%, #30C4A0 68.87%, #00B94F 180.55%);
    }
  }

  span {
    z-index: 2;
    position: relative;
    @extend %fontOpenSans;
    font-weight: bold;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: #FFFFFF;
  }
}

// BUTTON :: END
